.App {
  font-family: sans-serif;
  text-align: center;
}
body {
  display: flex;
  justify-content: center;
  background-color: rosybrown;
  position: relative;
  height: 100vh;
}
.App {
  display: flex;
  /* width: 400px; */
  /* height: 400px; */
  background-color: darkslategrey;
  justify-content: center;
  border: 2px solid black;
  border-radius: 24px;
  position: absolute;
}
.color-block {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid black;
  margin: 5px;
  /* position:absolute; */
}
.color-block:hover {
  cursor: pointer;
}
